import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-partner',
  templateUrl: './partner.component.html',
  styleUrls: ['./partner.component.scss']
})
export class PartnerComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent
  ) { }

  ngOnInit(): void {
  }
  gotoManageEmployee() {
    if (this.vhAuth.checkMyPermission("management_enable_employee")) this.router.navigate(['/cafe/dashboard/management/partner/employee']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoManageCustomer() {
    if (this.vhAuth.checkMyPermission("management_enable_customer")) this.router.navigate(['/cafe/dashboard/management/partner/customer']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoManageSupplier() {
    if (this.vhAuth.checkMyPermission("management_enable_supplier")) this.router.navigate(['/cafe/dashboard/management/partner/supplier']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  
  gotoCustomerGroup(){
    if (this.vhAuth.checkMyPermission("management_enable_customer_group")) this.router.navigate(['/cafe/dashboard/management/partner/customer-group'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")

  }

  gotoEmployeeGroup(){
    if (this.vhAuth.checkMyPermission("management_enable_employee_group")) this.router.navigate(['/cafe/dashboard/management/partner/employee-group'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")

  }

  gotoManageCustomerOnline(){
    // if (this.vhAuth.checkMyPermission("management_enable_customer")) 
    this.router.navigate(['/cafe/dashboard/management/partner/customer-online-list'])
    // else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
}
