import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PartnerComponent } from './partner.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule} from '../../../ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component: PartnerComponent
  },
  {
    path: 'employee',
    loadChildren: () => import('./employee/employee.module').then(m => m.EmployeeModule)
  },
  {
    path: 'customer',
    loadChildren: () => import('./customer/customer.module').then(m => m.CustomerModule)
  },
  {
    path: 'supplier',
    loadChildren: () => import('./supplier/supplier.module').then(m => m.SupplierModule)
  },
  {
    path: 'employee-group',
    loadChildren: () => import('./employee-group/employee-group.module').then(m => m.EmployeeGroupModule)
  },
  {
    path: 'customer-group',
    loadChildren: () => import('./customer-group/customer-group.module').then(m => m.CustomerGroupModule)
  },
  {
    path: 'customer-online-list',
    loadChildren: () => import('./customer-online-list/customer-online-list.module').then(m => m.CustomerOnlineListModule)
  }
]

@NgModule({
  declarations: [PartnerComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class PartnerModule { }
